.App {
  position: fixed;

  width: 100vw;
  height: 100vh;

  color: var(--font-main);
  text-align: center;

  background-color: var(--background-main);

  transition: color 0.5s ease, background-color 0.5s ease;
}

.Content {
  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100%;
  height: 100%;
}
